<template>
    <div class="area" ref="area_scroll" v-if="cityInfo">
        <div class="scroll_wrap">
            <div class="hot_wrap citylist">
                <div class="title"></div>
                <ul class="hot_city">
                    <li 
                    v-for="(item,index) in cityInfo.hotCities" 
                    :key="index"
                    @click="$emit('selectCity',item)"
                    >
                    {{item.name}}
                    </li>
                </ul>
            </div>
            <div class="city_wrap">
                <div class="city_content citylist" v-for="(item,index) in keys" :key="index">
                    <div class="title">{{item}}</div>
                    <ul>
                        <li @click="$emit('selectCity',city)" v-for="(city,index) in cityInfo[item]" :key="index">
                            {{city.name}}
                        </li>
                    </ul>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import BScroll from "better-scroll"
export default {
    props:{
        cityInfo:Object,
        keys:Array
    },
    created(){
        this.initScroll()
    },
    data(){
        return{
            bscroll:null
        }
    },
    methods:{
        initScroll(){
            this.bscroll=new BScroll(this.$refs.area_scroll,{
                click:true,
            })
        }
    }
}
</script>
<style scoped>
.area {
  margin-top: 10px;
  box-sizing: border-box;
  padding: 0 16px;
  background: #fff;
  height: calc(100% - 65px);
  overflow: hidden;
}
.scroll_wrap {
  background: #fff;
  overflow: auto;
}
.title {
  color: #aaa;
  padding: 15px 0;
}
.hot_city {
  padding: 0 16px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.hot_city li {
  width: 30%;
  background: #f1f1f1;
  margin: 0 10px 10px 0;
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  list-style: none;
}
.city_content li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
.area_keys {
  position: fixed;
  right: 0;
  top: 25%;
  color: #aaa;
  font-size: 12px;
  line-height: 1.4;
  text-align: center;
  padding: 0 5px;
}
</style>
